.top-section {
	padding: 50px 0 25px;
	background: url('../images/hp_top-bg.jpg') no-repeat center;
	background-size: cover;

	.container {
		@include respond-to('extra-large') {
			width: 1070px;
		}
	}
}

.top-section__h1-hp {
	height: 66px;
	background: url('../images/hp_title-sm.png') no-repeat center;
	background-size: contain;
	margin: 25px auto;
	letter-spacing: .7px;
	line-height: 1.2;
	@include respond-to('medium') {
		height: 48px;
		background: url('../images/hp_title.png') no-repeat center;
		background-size: contain;
	}
}

.top-section__rect {
	background: rgba(0, 0, 0, .5);
	color: $white;
	padding: 10px;
	@include respond-to('medium') {
		padding: 25px 40px 30px;
		margin: 45px 0;
	}
}

.top-section__rect-heading {
	font-size: 20px;
	text-align: center;
	text-transform: none;
	margin: 10px 0;
	@include respond-to('medium') {
		font-size: 25px;
	}
}

.top-section__icons {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	width: 80%;
	margin: 5px auto 0;
	@include respond-to('large') {
		margin: 20px auto 0;
		width: auto;
		flex-wrap: nowrap;
	}
}

.top-section__icon-block {
	flex-basis: 100%;
	margin-top: 15px;
	white-space: nowrap;
	@include respond-to('small') {
		flex-basis: 50%;
	}
	@include respond-to('large') {
		flex-basis: auto;
	}
}

.top-section__icon-block-desc {
	display: inline-block;
	vertical-align: top;
	margin-left: 12px;

	span {
		display: block;
		font-family: $font-regular;
		margin-bottom: 5px;
		@include respond-to('medium') {
			margin-bottom: 2px;
		}

		&:first-of-type {
			font-size: 20px;
			color: #f3a01f;
			letter-spacing: 1px;
		}

		&:last-of-type {
			font-size: 14px;
		}
	}
}

.top-section__button-arrow {
	display: block;
	margin-top: 6px;
	margin-bottom: -8px;
	@include respond-to('small') {
		display: inline;
		margin: 0;
	}
}
