@mixin respond-to($name) {
	//If the key exist in the map
	@if map-has-key($breakpoints, $name) {
		//Print a media query based on the value
		@media #{inspect(map-get($breakpoints, $name))} {
			@content;
		}
	}
}

@mixin retina-bg($width, $height, $bgx, $bgy, $rebuild: 'medium') {
	width: ($width / 2);
	height: ($height / 2);
	background-image: $sprite;
	background-position: ($bgx / 2) ($bgy / 2);
	background-size: $sprite-size2x;
	@include respond-to($rebuild) {
		width: $width;
		height: $height;
		background-size: $sprite-size-x $sprite-size-y;
		background-position: $bgx $bgy;
	}
}
