.btn {
	display: inline-block;
	width: 100%;
	border: 1px solid rgb(255, 211, 143);
	border-radius: 4px;
	background-color: rgb(243, 160, 31);
	color: $white;
	padding: 20px 10px;
	font-family: $font-bold;
	text-transform: uppercase;
	text-align: center;
	cursor: pointer;
}

.btn--glow {
	box-shadow: 0 0 10px 0 rgb(243, 160, 31);
	border: 1px solid #dd9017;
	border-top: 2px solid #f7c16d;
	letter-spacing: 1px;
	font-size: 16px;
	padding: 14px 10px;
}

.btn--purple {
	background-color: $color-main;
	border-color: $color-main;
}

.btn--sm-up-w488 {
	@include respond-to('small') {
		width: 488px;
	}
}
