.type--bold {
	font-family: $font-bold;
}

.type--nowrap {
	white-space: nowrap;
}

.type--relative {
	position: relative;
}

.type__inv-text {
	color: transparent;
}

.type--color-red {
	color: #f34f4f;
}

.type--danger {
	color: #be1616;
}

.type--text-big {
	font-size: 18px;
	font-family: $font-bold;
	@include respond-to('large') {
		font-size: 20px;
	}
}

.type--absolute-center {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.type--clear {
	&::after {
		content: '';
		display: block;
		clear: both;
	}
}

.type--bordered {
	border: 1px solid $color-main;
	display: inline-block;
	border-radius: 2px;
}

.type--clear-both {
	clear: both;
}

.type--block {
	display: block;
}

.type--italic {
	font-style: italic;
}

.type--center {
	text-align: center;
}

.type--color-white {
	color: $white;
}

.type--lg-ai-stretch {
	@include respond-to('large') {
		align-items: stretch;
	}
}

.type--my5 {
	margin-top: 5px !important;
	margin-bottom: 5px !important;
}

.type--my25 {
	margin-top: 25px !important;
	margin-bottom: 25px !important;
}

.type--color-main {
	color: $color-main;
}

.type--float-right {
	float: right;
}

.type--flex-stretch {
	display: flex;
	flex-direction: row;
	align-items: stretch;
}

.type--flex-grow-shrink {
	flex: 1 1 auto;
}

.type--vertical-center {
	vertical-align: middle;

	&::before {
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}

	* {
		vertical-align: middle;
	}
}

