$breakpoints: (
	'extra-small': (max-width: 576px),
	'small': (min-width: 576px),
	'medium': (min-width: 768px),
	'large': (min-width: 992px),
	'extra-large': (min-width: 1200px)
);

$mobile-menu-break-point: 'large';
$mobile-menu-height: 70px;
$mobile-menu-height-md-up: 96px;

$color-main:	#713494;
$color-main-secondary:	#ffa822;
$color-dark:	#222;
$white:			#fff;

$font-black: 'Conv_Roboto-Black';
$font-bold: 'Conv_Roboto-Bold';
$font-light: 'Conv_Roboto-Light';
$font-medium: 'Conv_Roboto-Medium';
$font-regular: 'Conv_Roboto-Regular';
$font-thin: 'Conv_Roboto-Thin';

$sprite: url('../images/sprite.png');
$sprite-size-x: 1000px;
$sprite-size-y: 1000px;
$sprite-size: $sprite-size-x $sprite-size-y;
$sprite-size2x: $sprite-size-x / 2 $sprite-size-y / 2;
