* {
	box-sizing: border-box;
}

html,
body {
	font-family: $font-light, sans-serif;
	font-size: 15px;
	color: $color-dark;
	font-weight: 400;
	overflow-x: hidden;
	max-width: 100%;
	position: relative;
	line-height: 1.3;
	background: #f8f8f8;
}

.container {
	padding-right: 10px;
	padding-left: 10px;
	@include respond-to('medium'){
		padding-right: 15px;
		padding-left: 15px;
	}
	@include respond-to('extra-large') {
		width: 1130px;
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-bold;
}

h1 {
	font-size: 36px;
}

h2 {
	font-size: 24px;
}

h3 {
	font-size: 21px;
}

h4 {
	font-size: 18px;
}

h5 {
	font-size: 15px;
}

p {
	margin: 5px 0 12px;
}

a {
	color: inherit;
	text-decoration: inherit;
	&:focus {
		outline: none;
		text-decoration: none;
	}
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
.bullet-list {
	a {
		color: $color-main;
		text-decoration: underline;
	}
}

img {
	display: inline-block;
	max-width: 100%;
	height: auto;
}
